.content {
  height: 80%;
  min-height: 92vh;
}

.container {
	display: table;
	vertical-align: middle;
	border: 0px;
	border-spacing: 0px;
}

.webgl-content * {
  border: 0;
  margin: 0;
  padding: 0
}

.webgl-content {
  position: relative;
  width: 100%;
}

.webgl-content .logo, .progressbar {
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}


.webgl-content .logo {
  background: url('/infohouse/LoadData/progressLogo.Light.png') no-repeat center / contain;
  width: 180px;
  height: 180px;
}

.webgl-content .progressbar {
  height: 18px;
  width: 200px;
  margin-top: 100px;
}

.webgl-content .progressbar .empty {
  background: url('/infohouse/LoadData/progressEmpty.Light.png') no-repeat right / cover;
  float: right;
  width: 100%;
  height: 100%;
  display: inline-block;
}

.webgl-content .progressbar .full {
  background: url('/infohouse/LoadData/progressFull.Light.png') no-repeat left / cover;
  float: left;
  width: 0%;
  height: 100%;
  display: inline-block;
}

.webgl-content .logo.Dark {
  background-image: url('/infohouse/LoadData/progressLogo.Dark.png');
}

.webgl-content .progressbar.Dark .empty {
  background-image: url('/infohouse/LoadData/progressEmpty.Dark.png');
}

.webgl-content .progressbar.Dark .full {
  background-image: url('/infohouse/LoadData/progressFull.Dark.png');
}

#fullscreen_request {
	position: absolute;
	bottom: 0;
	right: 0;
	min-height: 40px;
	min-width: 40px;
	margin-bottom: 20px;
	margin-right: 20px;
  z-index: 1;
  font-size: 25px;
}
