.zoom-image:hover {
  -ms-transform: scale(1.2); /* IE 9 */
  -webkit-transform: scale(1.2); /* Safari 3-8 */
  transform: scale(1.2);
}

.zoom-image {
  padding: 0rem;
  border-radius: 0rem;
  background-color: var(--main-enhancement-color);
  border: 2px solid var(--main-enhancement-color);
  transition: transform .2s;
  max-width: 100%;
  height: auto;
}

.zoom-image .cover-image {
  max-width: 100%;
  height: auto;
}
