  background-color: var(--main-color);
  background-image: inherit;
}

header.masthead.home .container{
  padding-top: 10vh;
}

header.masthead.home img {
  max-height: 60vh;
}

header.masthead.home .header-text {
  margin-top: 10vh;
}

header.masthead.home .header-text .fas {
  font-size: 3rem;
}

header.masthead.home .header-text a:hover {
