/* GLITCH*/
.glitch {
  &:before, &:after{
    content: "";
    background: url("../images/logo_full_gonemadstudios_white.png") no-repeat;
    -webkit-background-size: contain;
    -moz-background-size: contain;
    -o-background-size: contain;
    background-size: contain;
    background-position: center;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
  }
  &:before{
    left: 2px;
    -webkit-filter: drop-shadow(-1px -1px 0 #00ffea);
    filter:drop-shadow(-1px -1px 0 #00ffea);
    clip-path: inset(0% 0 100% 0);
    animation: glitch-anim 3s steps(1, end) infinite alternate-reverse;
  }
  &:after{
    left: -2px;
    -webkit-filter: drop-shadow(-1px -1px 0 #fe3a7f);
    filter:drop-shadow(-1px -1px 0 #fe3a7f);
    clip-path: inset(0% 0 100% 0);
    animation: glitch-anim 3s 0.5s steps(1, end) infinite alternate-reverse;
  }

  @keyframes glitch-anim {
    $steps: 5;
    $stopSteps: 40;

    @for $i from 0 through $steps {
      #{percentage($i*(1/($steps + $stopSteps)))} {
        $top: random(100);
        $bottom: random(101 - $top);
        clip-path: inset(#{$top}% 0 #{$bottom}% 0);
      }
    }

    @for $i from 0 through $stopSteps {
      #{percentage(($i + $steps)*(1/($steps + $stopSteps)))} {
        clip-path: inset(0% 0 100% 0);
      }
    }
  }
}

.about img.small {
  margin: 30px auto;
  padding: 0 20%;
}

.about .levels {
  width: 100%;
  padding: 0 10%;
  margin-top: 20px;
}

.partners, .projects, .games {
  background-color: var(--main-color);
}

.partners .logo {
  width: 20%;
}

.partners img {
  max-height: 14vmin;
}

.services .card-img-top {
  padding: 1rem;
  max-height: 160px;
  object-fit: contain;
}

.services .card .card-title, .games .card .card-title {
  font-weight: bold;
}

.services .row .mx-auto, .projects .row .mx-auto, .games .row .mx-auto {
  margin: 20px;
}

.services .row .card {
  height: 100%;
  box-shadow: 0 0 60px 0 rgba(10, 30, 80, .12);
  border: 2px solid rgba(0, 0, 0, 0.125);
  border-radius: 0rem;
}

.projects .vertical-center-items {
  display: flex;
  align-items: center;
}

.project .text-container {
  position: relative;
  height: min-content;
}

.project .left-box {
  position: absolute;
  bottom: -.9rem;
  left: -.9rem;
  width: 30%;
  height: 100%;
  border-left: 2px solid var(--main-enhancement-color);
  border-bottom: 2px solid var(--main-enhancement-color);
}

.project .right-box {
  position: absolute;
  bottom: -.9rem;
  right: -.9rem;
  width: 30%;
  height: 100%;
  border-right: 2px solid var(--main-enhancement-color);
  border-bottom: 2px solid var(--main-enhancement-color);
}

.games .card {
  color: var(--main-contrast-color);
  background: var(--main-enhancement-color);
  height: 100%;
  text-shadow: 2px 2px 10px #121212;
  border-radius: 0rem;
  border: 2px solid var(--main-enhancement-color);
  overflow: hidden;
}

.games .card .card-img-top {
  transition: transform .2s, opacity .2s;
}

.games .card:hover .card-img-top{
  -ms-transform: scale(1.1); /* IE 9 */
  -webkit-transform: scale(1.1); /* Safari 3-8 */
  transform: scale(1.1);
  opacity: .85;
}

.contact {
  min-height: 80vh;
  display: flex;
  align-items: center;
}

.contact-info {
  margin-top: 30px;
  font-size: 4vmin;
}

.contact-info .fa, .far, .fas {
  margin-right: 20px;
}
