header.hirilun {
  background-image:
    linear-gradient( rgba(0,0,0,.7), rgba(0,0,0,.3) )
}

header.hirilun .header-content {
  padding-top: 70px;
}

header.hirilun ul li a {
  font-size: 60px;
  line-height: 100px;
  display: block;
  width: 140px;
  height: 80px;
  color: rgba(242, 242, 242, 1);
  outline: none;
}

.hirilun-list {
  list-style-image: url('../../images/games/hirilun/common.png');
}

.hirilun-list .hirilun-item {
  margin: 0.6rem 0;
}

.hirilun-steam-seal {
  width: 60%;
}
