@import url('https://fonts.googleapis.com/css?family=Montserrat');

:root {
  --main-color: rgb(45, 41, 38);
  --main-contrast-color: rgb(255, 255, 255);
  --main-enhancement-color: rgb(5, 195, 221);
}

body,
html {
  width: 100%;
  height: 100%;
}

body {
  font-family: 'Montserrat', -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  overflow-x: hidden;
}

hr.hr-line {
  max-width: 100px;
  border-width: 1.8px;
  border-color: var(--main-enhancement-color);
  margin-bottom: 1.5rem !important;
  margin-top: 1.5rem !important;
}

hr.light {
  border-color: var(--main-contrast-color);
}

.light {
  color: var(--main-contrast-color);
  fill: var(--main-contrast-color);
}

a {
  color: var(--main-enhancement-color);
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
}

a:hover {
  color: var(--main-enhancement-color);
}

a.light {
  color: var(--main-contrast-color);
}

a.light:active, a.light:focus, a.light:hover {
  color: #D1D1D1;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: 'Montserrat', 'Open Sans', 'Helvetica Neue', Arial, sans-serif;
}

::-moz-selection {
  color: var(--main-contrast-color);
  background: var(--main-color);
  text-shadow: none;
}

::selection {
  color: var(--main-contrast-color);
  background: var(--main-color);
  text-shadow: none;
}

.light::-moz-selection, .light p::-moz-selection {
  color: var(--main-color);
  background: var(--main-contrast-color);
  text-shadow: none;
}

.light::selection, .light p::selection  {
  color: var(--main-color);
  background: var(--main-contrast-color);
  text-shadow: none;
}

img::-moz-selection {
  color: var(--main-contrast-color);
  background: transparent;
}

img::selection {
  color: var(--main-contrast-color);
  background: transparent;
}

img::-moz-selection {
  color: var(--main-contrast-color);
  background: transparent;
}


.text-faded {
  color: rgba(255, 255, 255, 0.7);
}

#content .no-header{
  padding-top: 90px;
}

section {
  padding: 5rem 0;
}

.section-heading {
  margin-top: 0;
}
