.separators {
  position: absolute;
  width: 100%;
}

.top-separator, .bottom-separator {
  position: relative;
  overflow: hidden;
  height: 180px;

  background-color: var(--main-contrast-color);
  /*background-image: linear-gradient(359deg, var(--main-contrast-color) 50%, var(--main-color) 50%);*/
}

.bottom-separator .top-line {
  height: 140px;

  background-color: var(--main-color);

  -ms-transform: skewY(-2.6deg) translateY(-70px); /* IE 9 */
  -webkit-transform: skewY(-2.6deg) translateY(-70px); /* Safari 3-8 */
  transform: skewY(-2.6deg) translateY(-70px);
}

.bottom-separator .middle-line {
  height: 180px;

  background-color: var(--main-enhancement-color);

  -ms-transform: skewY(-1.5deg) translateY(-70px); /* IE 9 */
  -webkit-transform: skewY(-1.5deg) translateY(-70px); /* Safari 3-8 */
  transform: skewY(-1.5deg) translateY(-70px);
}

.top-separator .top-line {
  height: 140px;

  background-color: var(--main-color);

  -ms-transform: skewY(1.5deg) translateY(130px); /* IE 9 */
  -webkit-transform: skewY(1.5deg) translateY(130px); /* Safari 3-8 */
  transform: skewY(1.5deg) translateY(130px);
}

.top-separator .middle-line {
  height: 180px;

  background-color: var(--main-enhancement-color);

  -ms-transform: skewY(2.6deg) translateY(90px); /* IE 9 */
  -webkit-transform: skewY(2.6deg) translateY(90px); /* Safari 3-8 */
  transform: skewY(2.6deg) translateY(90px);
}
