@import url(https://fonts.googleapis.com/css?family=Montserrat);
:root {
  --main-color: rgb(45, 41, 38);
  --main-contrast-color: rgb(255, 255, 255);
  --main-enhancement-color: rgb(5, 195, 221);
}

body,
html {
  width: 100%;
  height: 100%;
}

body {
  font-family: 'Montserrat', -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  overflow-x: hidden;
}

hr.hr-line {
  max-width: 100px;
  border-width: 1.8px;
  border-color: rgb(5, 195, 221);
  border-color: var(--main-enhancement-color);
  margin-bottom: 1.5rem !important;
  margin-top: 1.5rem !important;
}

hr.light {
  border-color: rgb(255, 255, 255);
  border-color: var(--main-contrast-color);
}

.light {
  color: rgb(255, 255, 255);
  color: var(--main-contrast-color);
  fill: rgb(255, 255, 255);
  fill: var(--main-contrast-color);
}

a {
  color: rgb(5, 195, 221);
  color: var(--main-enhancement-color);
  transition: all 0.2s;
}

a:hover {
  color: rgb(5, 195, 221);
  color: var(--main-enhancement-color);
}

a.light {
  color: rgb(255, 255, 255);
  color: var(--main-contrast-color);
}

a.light:active, a.light:focus, a.light:hover {
  color: #D1D1D1;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: 'Montserrat', 'Open Sans', 'Helvetica Neue', Arial, sans-serif;
}

::selection {
  color: rgb(255, 255, 255);
  color: var(--main-contrast-color);
  background: rgb(45, 41, 38);
  background: var(--main-color);
  text-shadow: none;
}

.light::selection, .light p::selection  {
  color: rgb(45, 41, 38);
  color: var(--main-color);
  background: rgb(255, 255, 255);
  background: var(--main-contrast-color);
  text-shadow: none;
}

img::selection {
  color: rgb(255, 255, 255);
  color: var(--main-contrast-color);
  background: transparent;
}

img::-moz-selection {
  color: rgb(255, 255, 255);
  color: var(--main-contrast-color);
  background: transparent;
}


.text-faded {
  color: rgba(255, 255, 255, 0.7);
}

#content .no-header{
  padding-top: 90px;
}

section {
  padding: 5rem 0;
}

.section-heading {
  margin-top: 0;
}

#mainNav {
  border-bottom: 1px solid var(--main-contrast-color);
  background-color: var(--main-color);
  font-family: 'Montserrat', 'Open Sans', 'Helvetica Neue', Arial, sans-serif;
  transition: all 0.2s;
}

#mainNav .navbar-brand {
  font-weight: 700;
  text-transform: uppercase;
  color: var(--main-contrast-color);
  font-family: 'Montserrat', 'Open Sans', 'Helvetica Neue', Arial, sans-serif;
}

#mainNav .navbar-logo {
  margin-right: 0.6vw;
}

#mainNav .navbar-brand:focus, #mainNav .navbar-brand:hover {
  color: #EBEBEB;
}

#mainNav ::-moz-selection, #mainNav ::selection {
  color: var(--main-color);
  background: var(--main-contrast-color);
  text-shadow: none;
}

#mainNav .navbar-nav > li.nav-item > a.nav-link,
#mainNav .navbar-nav > li.nav-item > a.nav-link:focus {
  font-size: .9rem;
  font-weight: 700;
  text-transform: uppercase;
  text-align: right;
  color: #FFF;
}

#mainNav .navbar-nav > li.nav-item > a.nav-link:hover,
#mainNav .navbar-nav > li.nav-item > a.nav-link:focus:hover {
  color: #EBEBEB;
}

#mainNav .navbar-nav > li.nav-item > a.nav-link.active,
#mainNav .navbar-nav > li.nav-item > a.nav-link:focus.active {
  color: #EBEBEB !important;
  background-color: transparent;
}

#mainNav .navbar-nav > li.nav-item > a.nav-link.active:hover,
#mainNav .navbar-nav > li.nav-item > a.nav-link:focus.active:hover {
  background-color: transparent;
}

@media (min-width: 992px) {
  #mainNav {
    border-color: transparent;
    background-color: transparent;
  }
  #mainNav .navbar-brand {
    opacity: 0;
    color: var(--main-contrast-color);
  }
  #mainNav .navbar-brand:focus, #mainNav .navbar-brand:hover {
    color: var(--main-contrast-color);
  }
  #mainNav .navbar-nav > li.nav-item > a.nav-link {
    padding: 0.5rem 1rem;
  }
  #mainNav .navbar-nav > li.nav-item > a.nav-link,
  #mainNav .navbar-nav > li.nav-item > a.nav-link:focus {
    color: rgba(255, 255, 255, 0.7);
  }
  #mainNav .navbar-nav > li.nav-item > a.nav-link:hover,
  #mainNav .navbar-nav > li.nav-item > a.nav-link:focus:hover {
    color: #FFF;
  }
  #mainNav.navbar-shrink {
    border-color: var(--main-contrast-color);
    background-color: var(--main-color);
  }
  #mainNav.navbar-shrink .navbar-brand {
    opacity: 1;
    color: #FFF;
  }
  #mainNav.navbar-shrink .navbar-brand:focus, #mainNav.navbar-shrink .navbar-brand:hover {
    color: #EBEBEB;
  }
  #mainNav.navbar-shrink .navbar-nav > li.nav-item > a.nav-link,
  #mainNav.navbar-shrink .navbar-nav > li.nav-item > a.nav-link:focus {
    color: #FFF;
  }
  #mainNav.navbar-shrink .navbar-nav > li.nav-item > a.nav-link:hover,
  #mainNav.navbar-shrink .navbar-nav > li.nav-item > a.nav-link:focus:hover {
    color: #EBEBEB;
  }
}

footer {
  padding: 20px 0;
  text-align: center;
  background-color: var(--main-color);
}

footer span.copyright {
  font-size: 90%;
  color: var(--main-contrast-color);
  line-height: 40px;
  text-transform: none;
  font-family: 'Montserrat', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', Arial, sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol', 'Noto Color Emoji';
}

footer img.coop {
  margin-left: 20px;
  height: 24px;
}

footer img.fse {
  margin-right: 20px;
  height: 30px;
}

footer ul.quicklinks {
  font-size: 90%;
  color: white;
  line-height: 40px;
  margin-bottom: 0;
  text-transform: none;
  font-family: 'Montserrat', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', Arial, sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol', 'Noto Color Emoji';
}

ul.social-buttons {
  margin-bottom: 0;
}

ul.social-buttons li a {
  font-size: 20px;
  line-height: 40px;
  display: block;
  width: 40px;
  height: 40px;
  transition: all 0.3s;
  color: var(--main-color);
  border-radius: 100%;
  outline: none;
  background-color: var(--main-contrast-color);
}

ul.social-buttons li a:active, ul.social-buttons li a:focus, ul.social-buttons li a:hover {
  background-color: var(--main-enhancement-color);
}

