.separators {
  position: absolute;
  width: 100%;
}

.top-separator, .bottom-separator {
  position: relative;
  overflow: hidden;
  height: 180px;

  background-color: var(--main-contrast-color);
  /*background-image: linear-gradient(359deg, var(--main-contrast-color) 50%, var(--main-color) 50%);*/
}

.bottom-separator .top-line {
  height: 140px;

  background-color: var(--main-color); /* IE 9 */
  -webkit-transform: skewY(-2.6deg) translateY(-70px); /* Safari 3-8 */
  transform: skewY(-2.6deg) translateY(-70px);
}

.bottom-separator .middle-line {
  height: 180px;

  background-color: var(--main-enhancement-color); /* IE 9 */
  -webkit-transform: skewY(-1.5deg) translateY(-70px); /* Safari 3-8 */
  transform: skewY(-1.5deg) translateY(-70px);
}

.top-separator .top-line {
  height: 140px;

  background-color: var(--main-color); /* IE 9 */
  -webkit-transform: skewY(1.5deg) translateY(130px); /* Safari 3-8 */
  transform: skewY(1.5deg) translateY(130px);
}

.top-separator .middle-line {
  height: 180px;

  background-color: var(--main-enhancement-color); /* IE 9 */
  -webkit-transform: skewY(2.6deg) translateY(90px); /* Safari 3-8 */
  transform: skewY(2.6deg) translateY(90px);
}

.main-color-svg {
  fill: var(--main-color);
}

.enhancement-color-svg {
  fill: var(--main-enhancement-color);
}

.zoom-image:hover { /* IE 9 */
  -webkit-transform: scale(1.2); /* Safari 3-8 */
  transform: scale(1.2);
}

.zoom-image {
  padding: 0rem;
  border-radius: 0rem;
  background-color: var(--main-enhancement-color);
  border: 2px solid var(--main-enhancement-color);
  transition: -webkit-transform .2s;
  transition: transform .2s;
  transition: transform .2s, -webkit-transform .2s;
  max-width: 100%;
  height: auto;
}

.zoom-image .cover-image {
  max-width: 100%;
  height: auto;
}

.content {
  height: 80%;
  min-height: 92vh;
}

.container {
	display: table;
	vertical-align: middle;
	border: 0px;
	border-spacing: 0px;
}

.webgl-content * {
  border: 0;
  margin: 0;
  padding: 0
}

.webgl-content {
  position: relative;
  width: 100%;
}

.webgl-content .logo, .progressbar {
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}


.webgl-content .logo {
  background: url('/infohouse/LoadData/progressLogo.Light.png') no-repeat center / contain;
  width: 180px;
  height: 180px;
}

.webgl-content .progressbar {
  height: 18px;
  width: 200px;
  margin-top: 100px;
}

.webgl-content .progressbar .empty {
  background: url('/infohouse/LoadData/progressEmpty.Light.png') no-repeat right / cover;
  float: right;
  width: 100%;
  height: 100%;
  display: inline-block;
}

.webgl-content .progressbar .full {
  background: url('/infohouse/LoadData/progressFull.Light.png') no-repeat left / cover;
  float: left;
  width: 0%;
  height: 100%;
  display: inline-block;
}

.webgl-content .logo.Dark {
  background-image: url('/infohouse/LoadData/progressLogo.Dark.png');
}

.webgl-content .progressbar.Dark .empty {
  background-image: url('/infohouse/LoadData/progressEmpty.Dark.png');
}

.webgl-content .progressbar.Dark .full {
  background-image: url('/infohouse/LoadData/progressFull.Dark.png');
}

#fullscreen_request {
	position: absolute;
	bottom: 0;
	right: 0;
	min-height: 40px;
	min-width: 40px;
	margin-bottom: 20px;
	margin-right: 20px;
  z-index: 1;
