}

header.masthead {
  padding-top: 10rem;
  padding-bottom: calc(10rem - 56px);
  background-image:
    linear-gradient( rgba(0,0,0,.7), rgba(0,0,0,.3) );
  background-position: center center;
  background-size: cover;
}

header.masthead hr {
  margin-top: 30px;
  margin-bottom: 30px;
}

header.masthead h1 {
  font-size: 1.4rem;
}

header.masthead p {
  font-weight: 300;
}

header.masthead img {
  width: 100%;
  height: 100%;
  object-fit: contain;
  max-height: 20vh;
}

header .header-text {
  text-shadow: 2px 2px 6px #161515;
}

header .header-text ::-moz-selection, header .header-text ::selection {
  color: #1D2022;
  background: #EBEBEB;
  text-shadow: none;
}

header .contact-button {
  display: inline-block;
  font-weight: 500;
  color: #FFFFFF;
  text-align: center;
  vertical-align: middle;
  -webkit-user-select: none;
  user-select: none;
  background-color: transparent;
  border: 2px solid transparent;
  border-top-color: transparent;
  border-right-color: transparent;
  border-bottom-color: transparent;
  border-left-color: transparent;
  padding: 0.8rem 1.8rem;
  font-size: 1.22rem;
  line-height: 1.6;
  border-radius: 1rem;
  transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out, border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
  background-color: #272B30;
  border-color: #34D1E4;
}

header .contact-button:hover {
  background-color: #34D1E4;
  text-decoration: none;
}

header .contact-button:active, header .contact-button:focus {
  background-color: #64D5E3;
  box-shadow: 0 0 0.2rem 0.2rem rgba(52, 209, 228, 0.25);
}

@media (min-width: 768px) {
  header.masthead p {
    font-size: 1.15rem;
  }
}

@media (min-width: 992px) {
  header.masthead {
    height: 100vh;
    min-height: 650px;
    padding-top: 0;
    padding-bottom: 0;
  }

  header.masthead h1 {
    font-size: 3rem;
  }

  header.masthead img {
    max-height: 40vh;
  }
}

@media (min-width: 1200px) {
  header.masthead h1 {
    font-size: 4rem;
  }
}

