.video-background {
  display: flex;
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -99;
}

.video-foreground {
    width: 100%;
    height: 100%;
    overflow: hidden;
    display: grid;
    justify-content: center;
}

.video-foreground .video-iframe {
  width: 100vw;
  height: 56vw;
  pointer-events: none;
  display: inline;
}

@media (min-aspect-ratio: 16/9) {
  .video-foreground .video-iframe {
    width: 100vw;
    height: 56vw;
  }
}

@media (max-aspect-ratio: 16/9) {
  .video-foreground .video-iframe {
    width: 178vh;
    height: 100vh;
  }
}

